html {
  scroll-behavior: smooth;
  font-size: 1rem; }

body {
  font-size: 16px;
  font-size: 1rem;
  font-family: 'proxima-nova', sans-serif;
  font-weight: 400;
  line-height: 1.4;
  background-color: #fff;
  position: relative; }

/*
*
* Grid Layout
*/
.container {
  width: 100%;
  margin: 0 auto; }

.row {
  display: flex;
  padding-left: 1.5rem;
  padding-right: 1.5rem; }

.col-4 {
  width: 33.3333%; }

.col-6 {
  width: 50%; }

.col-8 {
  width: 66.3333%; }

.col-12 {
  width: 100%; }

.container:before,
.container:after,
.row:before,
.row:after {
  content: " ";
  display: table; }

.container:after,
.row:after {
  clear: both; }

.space-between {
  justify-content: space-between; }

/*
*
* Typography
*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'proxima-nova-condensed';
  line-height: 1.2;
  color: #212121; }

p {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 1rem;
  color: #212121; }

/*
*
* Elements
*/
img,
.img-responsive {
  max-width: 100%;
  display: inline-block; }

.mb-2 {
  margin-bottom: 2rem; }

.site-header {
  height: 3rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 8; }

.site-logo,
.site-nav {
  display: flex;
  align-items: center;
  height: 3rem; }

.site-logo img {
  height: 2rem;
  width: 2rem;
  border-radius: 50%; }

.site-nav {
  float: right; }
  .site-nav img {
    height: 1.125rem; }

.intro,
.contact-btn,
.nav.desktop {
  display: none; }

@media (min-width: 768px) {
  .site {
    width: 100%;
    display: flex;
    flex-direction: row; }
    .site .site-header {
      width: 33.33333%; }
      .site .site-header .container {
        padding-left: 5.25rem;
        width: calc( 100% - 5.25rem); }
    .site .site-content {
      width: 66.3333%; }
      .site .site-content .container {
        padding-right: 5.25rem;
        width: calc( 100% - 5.25rem); }
  .site-main {
    width: 25rem; }
  .col-8 {
    width: 100%; }
  .site-header {
    min-height: 100vh;
    box-shadow: none;
    top: 0;
    bottom: 0;
    position: fixed;
    overflow-y: scroll;
    overflow-x: hidden; }
  .site-header::-webkit-scrollbar {
    display: none; }
  .site-content {
    margin-left: 33.3333%; }
  .site-logo {
    display: inline-block;
    height: auto;
    width: 100%;
    padding-top: 4rem; }
    .site-logo img {
      height: 7.5rem;
      width: 7.5rem; }
  .intro {
    display: inline-block;
    width: 100%; }
    .intro .name {
      font-size: 2rem;
      margin-bottom: 0; }
    .intro .title {
      color: #757575;
      font-size: 1.125rem;
      line-height: 1.2;
      font-weight: 400; }
  .contact-btn {
    height: 2rem;
    width: 8.5rem;
    border: 1px solid #212121;
    background-color: transparent;
    border-radius: 1.5rem;
    display: flex;
    align-items: center;
    padding-left: 0.125rem;
    padding-right: 0.125rem;
    position: fixed;
    bottom: 3rem; }
    .contact-btn:hover .arrow {
      width: 8.5rem; }
    .contact-btn:hover span {
      color: #fff; }
    .contact-btn .arrow {
      background-color: #212121;
      height: 1.875rem;
      width: 1.875rem;
      border-radius: 2rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      position: relative;
      transition: 0.5s; }
      .contact-btn .arrow img {
        height: 0.875rem;
        width: auto;
        margin-left: 0.375rem; }
    .contact-btn span {
      font-size: 0.875rem;
      font-weight: 400;
      text-transform: uppercase;
      position: absolute;
      left: 2.5rem;
      transition: 0.5s; }
    .contact-btn a {
      background-color: transparent;
      z-index: 8;
      border-radius: 2.25rem; }
  .mobile-menu,
  .site-header .col-4,
  .contact-me {
    display: none; }
  .nav.desktop {
    display: inline-block;
    width: 100%;
    margin-bottom: 6rem; } }

@media (min-width: 768px) and (max-width: 1000px) {
  .nav li a {
    line-height: inherit; } }

@media (max-resolution: 160dpi) {
  .site-main {
    width: 35rem; }
  .intro .title {
    width: 17rem; }
  .contact-btn {
    height: 3rem;
    width: 11.5rem;
    border-radius: 2.25rem;
    padding-left: 0.25rem;
    padding-right: 0.25rem; }
    .contact-btn:hover .arrow {
      width: 11.5rem; }
    .contact-btn .arrow {
      height: 2.5rem;
      width: 2.5rem;
      border-radius: 2.5rem; }
      .contact-btn .arrow img {
        height: 1.125rem;
        margin-left: 0.25rem;
        margin-left: 0.5rem; }
    .contact-btn span {
      font-size: 1.125rem;
      left: 3rem; } }

.mobile-menu {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 10;
  top: 0;
  left: -100%;
  background-color: #fff;
  overflow: hidden;
  transition: 0.5s; }
  .mobile-menu header {
    width: 100%;
    height: 3rem;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
    .mobile-menu header img {
      height: 1.25rem;
      margin-right: 1rem;
      padding: 0.675rem;
      border-radius: 50%; }
  .mobile-menu footer {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    height: 3rem; }

.nav {
  margin: 0;
  padding: 0 1.5rem;
  list-style: none;
  width: 100%;
  height: calc( 100vh - 9rem);
  margin-top: 3rem; }
  .nav li {
    width: 100%;
    display: inline-block;
    margin: 1rem 0; }
  .nav a {
    font-size: 2rem;
    color: #757575;
    text-decoration: none;
    font-weight: 700;
    line-height: 0;
    display: inline-block; }
    .nav a.active {
      color: #212121; }

@media (min-width: 768px) {
  .nav {
    justify-content: inherit;
    padding: 0;
    margin-top: 2rem;
    height: auto; } }

.filp-text {
  overflow: hidden;
  width: 100%;
  margin-top: 5.25rem;
  height: 4.875rem;
  margin-bottom: 2.5rem;
  display: inline-block; }

ul.flip {
  margin: 0;
  padding: 0;
  text-align: left;
  list-style: none;
  animation: 6s linear 0s normal none infinite change;
  -webkit-animation: 6s linear 0s normal none infinite change;
  -moz-animation: 6s linear 0s normal none infinite change;
  -o-animation: 6s linear 0s normal none infinite change; }

ul.flip li {
  font-size: 4rem;
  line-height: 4.875rem;
  margin: 0;
  font-size: 4rem;
  font-weight: 700;
  color: #212121;
  font-family: 'proxima-nova-condensed'; }

.description {
  font-size: 1.25rem;
  line-height: 1.6;
  position: relative; }
  .description span {
    font-weight: 600;
    border-bottom: 0.125rem solid #212121; }
    .description span:hover .info {
      visibility: visible; }
    .description span .info {
      visibility: hidden;
      font-size: 1rem;
      font-weight: normal;
      border-bottom: 0;
      padding: 1rem;
      width: 15rem;
      height: 12rem;
      background-color: #fff;
      position: absolute;
      top: -2rem;
      left: calc(44% - 7.5rem);
      margin-left: 0;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.16);
      border-radius: 0.25rem;
      z-index: 4;
      display: flex;
      align-items: center; }

.heading {
  color: #212121;
  font-size: 1.25rem;
  line-height: 1.2;
  text-transform: uppercase;
  font-weight: 400;
  position: relative;
  margin-top: 6rem;
  font-family: 'proxima-nova', sans-serif; }
  .heading:before {
    content: "";
    width: 1.5rem;
    height: 0.175rem;
    background-color: #212121;
    position: absolute;
    z-index: 6;
    top: -1.25rem;
    left: 0; }

.recent-work {
  margin-bottom: 3rem;
  position: relative; }
  .recent-work h1 {
    font-size: 1.5rem;
    line-height: 1.2;
    margin-bottom: 0.5rem;
    font-family: 'proxima-nova', sans-serif; }
  .recent-work p {
    color: #757575;
    margin-bottom: 0; }

.link-overlay {
  background-color: transparent;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2; }

.works {
  display: inline-block; }
  .works .recent-work:nth-child(2) h1 {
    color: #0984e3; }
  .works .recent-work:nth-child(3) h1 {
    color: #6c5ce7; }
  .works .recent-work:nth-child(4) h1 {
    color: #fd79a8; }

.site-footer {
  margin-top: 10rem; }

.copyright {
  border-top: solid 1px #d1d1d1;
  padding-top: 1rem;
  width: 100%;
  display: inline-block;
  color: #212121;
  line-height: 1; }

.contact-me {
  background-color: #212121;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  cursor: pointer; }
  .contact-me a {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  .contact-me img {
    height: 1.125rem;
    width: auto;
    margin-left: -0.125rem; }
